import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { Http, BibleVerseAPI } from '../http-api';
import { HttpModule} from '@angular/http';
import { HTTP } from '@ionic-native/http';
import { LocalNotifications } from '@ionic-native/local-notifications';
import { MyApp } from './app.component';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { GlobalProvider } from '../providers/global/global';
import { NonnieModule } from '../pages/nonnie/nonnie.module';
import { UserModule } from '../pages/user/user.module';

@NgModule({
  declarations: [
    MyApp,
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/nonnie/sign-out-nonnie/sign-out.module.ngfactory#SignOutPageModuleNgFactory', name: 'SignOutNonniePage', segment: 'sign-out', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/nonnie/verify/verify-edit-weekend/verify-edit-weekend.module.ngfactory#VerifyEditWeekendPageModuleNgFactory', name: 'VerifyEditWeekendPage', segment: 'verify-edit-weekend', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/nonnie/verify/verify-edit/verify-edit.module.ngfactory#VerifyEditPageModuleNgFactory', name: 'VerifyEditPage', segment: 'verify-edit', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/nonnie/verify/verify.module.ngfactory#VerifyPageModuleNgFactory', name: 'VerifyPage', segment: 'verify', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/nonnie/weekend-nonnie/weekend.module.ngfactory#WeekendPageModuleNgFactory', name: 'WeekendNonniePage', segment: 'weekend', priority: 'low', defaultHistory: [] }
  ]
}),
    HttpModule,
    NonnieModule,
    UserModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    Http,
    BibleVerseAPI,
    HTTP,
    GlobalProvider,
    LocalNotifications
  ]
})
export class AppModule {}



